import {
  KafkaCallbackResponse,
  XodBaseMessageType,
  XodSourceMessageType,
} from '@xip-online-data/types';

export interface KafkaSourceInterface {
  setCallbackFunction(
    callbackFunction: (
      message: XodBaseMessageType,
    ) => Promise<KafkaCallbackResponse>,
    eventType?: string,
    identifier?: string,
  ): void;
  init(): Promise<void>;
  send(
    messages: Array<XodSourceMessageType>,
    topic: string,
    additionalMetaData?: Partial<XodBaseMessageType>,
  ): Promise<boolean>;
  sendIndividual(
    messages: Array<XodSourceMessageType>,
    topic: string,
  ): Promise<boolean>;
  exitProcess(message: string): Promise<void>;
}
